<template>
  <div id="app">
    <div class="container-fluid">
      <Header/>
      <hr style="border: none">
      <div class="row mt-5 student-reg">
        <div class="text-center" >
          <div class="main-title">忘记密码</div>
          <div class="row mt-6">
            <div class="center-block" style="width:300px;">
              <div class="row text-center">
                <el-form  class="center-block mt-3" ref="form" :model="form" :rules="rules" status-icon :show-message="true">
                  <el-form-item prop="phone">
                    <el-input placeholder="你的手机号" autocomplete="off" v-model="form.phone"></el-input>
                  </el-form-item>
                  <el-form-item  prop="verify">
                    <el-input placeholder="验证码" autocomplete="off" v-model="form.verify"></el-input>
                    <el-popover
                      placement="right"
                      trigger="click">
                      <slide-verify :l="42"
                                    :r="10"
                                    :w="310"
                                    :h="155"
                                    @success="onSuccess"
                                    @fail="onFail"
                                    @refresh="onRefresh"
                                    @changeNum = "change"
                                    :sliderText="'请向右滑动'"
                                    :setNum = "60"
                      ></slide-verify>
                      <div>{{msg}}</div>
                      <el-button  slot="reference" :disabled="countdownshow" class="verifycode el-input__inner" style="width: 83px;"><span v-show="!countdownshow">{{verifyCodeText}}</span><span v-show="countdownshow">{{countdown}}s</span></el-button>
                    </el-popover>
                  </el-form-item>
                  <el-form-item  prop="pwd">
                    <el-input placeholder="设置密码" autocomplete="off" v-model="form.pwd" show-password></el-input>
                  </el-form-item>
                  <div class="row" style="width: 330px;margin: unset" v-show="this.form.pwd != ''">
                    <div class="pull-right" style="display: flex">
                      <div :class="level != 0?'level-color':''" style="width:41px;height:5px;background:#E8E8E8;border-radius:3px;margin-right: 5px"></div>
                      <div :class="level ==2 || level == 3?'level-color':''" style="width:41px;height:5px;background:#E8E8E8;border-radius:3px;margin-right: 5px"></div>
                      <div :class="level == 3?'level-color':''" style="width:41px;height:5px;background:#E8E8E8;border-radius:3px;"></div>
                    </div>
                  </div>
                  <el-form-item prop="pwd">
                    <el-input placeholder="确认密码" v-model="form.pwd2" show-password></el-input>
                  </el-form-item>
                </el-form>
              </div>
              <div class="row mt-6">
                <div class="btn btn-block reg-btn" @click="resetPwd('form')">重置密码</div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Footer/>
    </div>
  </div>
</template>

<script>
import Header from '../../components/reg/Header.vue'
import Footer from '../../components/reg/Footer.vue'
export default {
  name: 'app',
  components: {
    Header,
    Footer
  },
  data () {
    return {
      msg: '',
      level: '', // 密码强度等级
      verifyCodeText: '获取验证码', // 验证码按钮显示内容
      interval: null, // 验证码码定时器
      countdown: '', // 验证码倒计时秒数
      countdownshow: false, // 验证码倒计时秒数显示标志
      form: {
        phone: '',
        verify: '',
        pwd: '',
        pwd2: ''
      },
      rules: {
        phone: [
          { required: true, message: '请输入11位手机号码', trigger: 'blur' },
          { min: 11, max: 11, message: '长度11位', trigger: 'blur' }
        ],
        verify: [
          { required: true, message: '请输入短信验证码', trigger: 'blur' }
        ],
        pwd: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 30, message: '长度6-30位', trigger: 'blur' }
        ],
        pwd2: [
          { required: true, message: '请输入确认密码', trigger: 'blur' }
        ]
      }
    }
  },
  computed: {
  },
  watch: {
    'form.pwd': {
      deep: true,
      handler: function () {
        var pwd = this.form.pwd
        var reg1 = /\d/
        var reg2 = /[a-zA-Z]/
        var reg3 = /\W/
        var level = 0
        if (reg1.test(pwd)) {
          // 密码等级增加
          level++
        }
        if (reg2.test(pwd)) {
          // 密码等级增加
          level++
        }
        if (reg3.test(pwd)) {
          // 密码等级增加
          level++
        }
        console.log(level)
        this.level = level
      }
    }
  },
  methods: {
    onSuccess () {
      let self = this
      this.msg = '验证成功,请注意查收短信。'
      this.countdown = 60
      this.countdownshow = true
      // 开始计时
      this.interval = setInterval(() => {
        this.countdown--
        if (this.countdown <= 0) {
          this.verifyCodeText = '重新发送'
          this.countdownshow = false
          clearInterval(this.interval)
        }
      }, 1000)
      this.$http.post('/user/vcfraudprevent', {}, res => {
        if (res.success) {
          self.$http.post('/user/sendsmsverificationcode', {
            'phone': self.form.phone,
            'msgType': 'register',
            'sign': res.data.sign
          }, res => {
            if (res.success) {

            }
          })
        } else {
        }
      })
    },
    onFail () {
      this.msg = ''
    },
    onRefresh () {
      console.log('ok----')
    },
    change (num) {
      console.log('num', num)
    },
    resetPwd (formName) {
      let me = this
      this.$refs[formName].validate((valid) => {
        if (valid) {
          me.$http.post('/user/resetpassword', {
            'userCellphone': me.form.phone,
            'userPassword': me.form.pwd,
            'smsVerificationCode': me.form.verify
          }, res => {
            if (res.success) {
              window.location = 'login.html'
            }
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
  }
}
</script>

